import React, { useState, useEffect } from 'react'

const  RankImage = ({ src, ...props }) => {

  const rank_levelup = `https://tkdw-img.s3.ap-northeast-2.amazonaws.com/license/grade/${src}.png`;

  const [rank, setRank] = useState('')

  useEffect(() => {
    if(rank_levelup){
      setRank(rank_levelup)
    }
  }, [rank_levelup])

  switch(src) {
    case '단': case '1단': case '2단': case '3단': case '4단': case '5단': case '6단': case '7단': case '8단': case '9단': 
      return <img src={rank} className="w-full" alt="rank, 단" {...props} />
    case '품' : case '1품': case '2품': case '3품': case '4품': case '5품': case '6품': case '7품': case '8품': case '9품':
      return <img src={rank} className="w-full" alt="rank, 품" {...props} />
    case '1급': case '2급': case '3급': case '4급': case '5급': case '6급': case '7급': case '8급': case '9급':
      return <img src={rank} className="w-full" alt="rank, 급" {...props} />
    default : return null
  }

}

export default RankImage