import React from 'react';
import InfoData from '../../../../components/UI/Atoms/Info/InfoData';
import CertificateProfile from '../../../../components/UI/Atoms/Profile/CertificateProfile';
import RankLevelupImage from '../../../../components/common/Rank/RankLevelupImage';
import moment from 'moment/moment';
import { getFullname } from '../../../../utils/render/getFullname';

const LevelInfo = ({ data }) => {
  return (
    <>
      <div className="h-full">
        <div id="levelup_rank" className="w-[70px] h-[50px] absolute top-[255px] left-[321px]">
          <RankLevelupImage src={data?.level_after || ''} />
        </div>
        <div id="levelup_profile" className="absolute top-[180px] left-[105px]">
          <CertificateProfile width={99} height={127.5} shape={0} urlKey={data?.photo_url || ''} />
        </div>
        <div
          id="levelup_info"
          className="absolute top-[297px] left-[220px] flex flex-col justify-center gap-[16px] mt-[18px]"
        >
          <InfoData className="text-[14px] ml-[-25px]">KR</InfoData>
          <InfoData className="text-[14px] ml-[-55px]">대한민국</InfoData>
          <InfoData className="text-[14px] ml-[-55px]">
            {getFullname(data?.last_name, data?.first_name)}
          </InfoData>
          <InfoData className="text-[14px] ml-[-45px]">
            {moment(data?.dob).format('YYYY년 MM월 DD일')}
          </InfoData>
          <InfoData className="text-[14px] mt-[3px] ml-[-55px]">{data?.dojang_name || ''}</InfoData>
        </div>
        <div className="text-[14px] tracking-normal absolute bottom-[200px] left-[105px] text-[#777777] w-[480px]">
          <div className="mb-[10px]">
            위 사람은 본원 심사규정에 따른 태권도 4급 승급 심사에 <br />
            합격하였으므로 이 증서를 수여함.
          </div>
          <div>
            The above named person has attained the 4th Keub in the TaeKwonDo Promotion Test
            conducted by the Yang Jin Bang<br /> The Korea Tae Kwon Do Association.
          </div>
          <div></div>
        </div>
        <div id="levelup_date" className="absolute bottom-[150px] left-[280px] text-[#777777]">
          <InfoData className="text-[14px]">
            {moment(data?.levelup_date || '').format('YYYY년 MM월 DD일')}
          </InfoData>
        </div>
        <div className="flex absolute bottom-[50px] left-[260px]">
          <div>
            <div className="text-[30px] tracking-widest">태극태권도장</div>
            <div className="flex text-[20px]">
              <div className="w-[50px]">관장</div>
              <div className="w-[125px] text-end">김태권</div>
            </div>
          </div>
          <img
            src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/license/stamp.png"
            alt="태권월드 도장"
            className="w-[80px] absolute right-[-80px]"
          />
        </div>
      </div>
    </>
  );
};

export default LevelInfo;
