import React from 'react'
import styled from 'styled-components';

const BtnStyle = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 5px;
  background-color: none;
  background-image: url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/charm_arrow-left.png');
  cursor: pointer;

  &:hover {
    background-color: #B5B5B5;
  }
`;


const BackBtnSM = ({onClick, children}) => {
  return (
    <BtnStyle className="mr-[10px] flex items-center justify-center" 
              onClick={onClick}>
              {children}
    </BtnStyle>
  )
}

export default BackBtnSM