import React, { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import { useRecoilValue } from 'recoil';
import { getLevelupList } from '../../../../api/Levelup/levelupApi';
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD';
import { Grid } from '../../../../components/common/Grid/Grid';
import LevelupCertificate from '../../../../components/common/Levelup/LevelupCertificate';
import MonthSelect from '../../../../components/common/Select/MonthSelect';
import YearSelect from '../../../../components/common/Select/YearSelect';
import PageContainer from '../../../../components/layout/PageContainer';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';
import Description from '../../../../components/UI/Atoms/Ex/Description';
import Em from '../../../../components/UI/Atoms/Ex/Em';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import { useModal } from '../../../../hooks/basic/useModal';
import { useSelect } from '../../../../hooks/basic/useSelect';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';
import { getCurrMonth, getCurrYear } from '../../../../utils/basic/getDate';
import RankImage from '../../../../components/common/Rank/RankImage';
import ModalContainerLV from '../../../../components/layout/ModalContainerLV';
import { toAge } from '../../../../utils/render/toAge';
import { toGender } from '../../../../utils/render/toGender';
import { getFullname } from '../../../../utils/render/getFullname';
import NameSearchInput from '../../../../components/UI/Atoms/Input/NameSearchInput';

const LevelupHistoryPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom);
  let yy = getCurrYear().toString();
  let mm = getCurrMonth().toString();

  const [totalData, setTotalData] = useState('');

  // custom hook
  const [classId, handleChangeClasses] = useSelect('0');
  const [year, handleYear] = useSelect(yy);
  const [month, handleMonth] = useSelect(mm);
  const [openCert, openCertFn, closeCertFn] = useModal();

  // query
  const { data } = useQuery(
    ['getLevelupList', dojangId, classId, year, month],
    () => getLevelupList(dojangId, classId, year, month),
    {
      enabled: !!dojangId && !!classId,
    },
  );

  const handleCertificate = (e) => {
    let index = e.target.id;
    setTotalData(data[index]);
    openCertFn();
  };

  return (
    <PageContainer>
      <div className="items-center mb-3 w-full">
        <div className="flex justify-between">
          <div className="flex gap-[5px]">
            <ClassSelectMD value={classId} onChange={handleChangeClasses} />
            <YearSelect value={year} onChange={handleYear} />
            <MonthSelect value={month} onChange={handleMonth} />
          </div>
          {/* <div>
            <NameSearchInput placeholder={'이름을 검색해주세요.'} />
          </div> */}
        </div>
      </div>
      <Grid>
        <Grid.Head>
          <Description>
            {year}년 {month}월의 승급자는 <Em>{data?.length}명</Em> 입니다.
          </Description>
        </Grid.Head>
        <Grid.RowWrap>
          {data.length <= 0 && (
            <div className="flex justify-center items-center h-full">데이터가 없습니다.</div>
          )}
          {data &&
            React.Children.toArray(
              data?.map((item, idx) => (
                <Grid.Row className="px-[20px]">
                  <Grid.Col w={1} className="p-0">
                    <ProfileImage urlKey={item?.photo_url || ''} />
                  </Grid.Col>
                  <Grid.Col w={2} className="p-0">
                    {getFullname(item?.last_name || '', item?.first_name || '')}
                  </Grid.Col>
                  <Grid.Col w={1} className="p-0 pt-[5px]">
                    <RankImage src={item?.level_before || ''} />
                  </Grid.Col>
                  <Grid.Col w={1} className="p-0">
                    {toGender(item?.sex || '')}
                  </Grid.Col>
                  <Grid.Col w={1} className="p-0">
                    {toAge(item?.dob || '')}
                  </Grid.Col>
                  <Grid.Col w={2} className="p-0">
                    {item?.class_title || ''}
                  </Grid.Col>
                  <Grid.Col w={2} className="p-0">
                    <div className="flex flex-col gap-[2px]">
                      <span className="text-[12px] text-[#5E81F4]">승급일</span>
                      <span>{item?.levelup_date.substr(0, 10) || ''}</span>
                    </div>
                  </Grid.Col>
                  <Grid.Col w={2} className="p-0">
                    <div className="flex flex-col gap-[2px]">
                      <span className="text-[12px] text-[#5E81F4]">승급 급수</span>
                      <span className="flex items-center">
                        {item?.level_after || ''}{' '}
                        <RankImage src={item?.level_after || ''} className="ml-[5px]" />
                      </span>
                    </div>
                  </Grid.Col>
                  <Grid.Col w={1} className="p-0">
                    {item?.level_after?.slice(-1) == '급' && (
                      <BlackBtnSM id={`${idx}`} onClick={handleCertificate}>
                        승급증
                      </BlackBtnSM>
                    )}
                  </Grid.Col>
                </Grid.Row>
              )),
            )}
        </Grid.RowWrap>
      </Grid>

      <ModalContainerLV open={openCert} onClose={closeCertFn} doc>
        <LevelupCertificate data={totalData} />
      </ModalContainerLV>
    </PageContainer>
  );
};

export default LevelupHistoryPage;
