import React from 'react'
import { styled } from '@mui/system';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box'
import BackBtnSM from '../UI/Atoms/Button/BackBtn';

const DocStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 630,
  height: 895,
  bgcolor: '#ffffff',
  overflow:'auto',
};

const ModalContainerLV = ({ doc, open, onClose, title, children, }) => {
  return (
    <>
    <Modal
      open={open}
      onClose={onClose}
    >
      <Box sx={DocStyle}>
        { !doc &&
          <div className='flex items-center border-solid border-b border-x-0 border-t-0 border-[#aaaaaa] pb-[15px]'>
            <BackBtnSM onClick={onClose}></BackBtnSM>
            <div className='text-[24px] font-bold'>
              {title}
            </div>
          </div>
        }

        {children}
      </Box>
    </Modal>
    </>
  )
}

export default ModalContainerLV
