import authApi from "../authApi"

export const getInfoForLevelup = async (dojangId, kwanjangId, studentId) => {
  const res = await authApi.get(`student/studentlevelupinforead/${dojangId}/${kwanjangId}/${studentId}`)
  return res.data[0]
}

// 승급 이력 조회
export const getLevelupList = async (dojangId, classId, year, month) => {
  const res = await authApi.get(`student/studentlevelupreadbymonth/${dojangId}/${classId}/${year}/${month}`)
  return res.data;
}

// 승급 등록
export const postLevelup = async (dojangId, studentId, body) => {
  const res = await authApi.post(`student/studentlevelupcreate/${dojangId}/${studentId}`, body)
  return res.data
}