import React, { useRef } from 'react';
import LevelInfo from '../../../pages/UserDojang/Manage/Level/LevelInfo';
import ReactToPrint from 'react-to-print';
import EditBtnSM from '../../UI/Atoms/Button/EditBtnSM';
import PrintIcon from '@mui/icons-material/Print';

const LevelUpImg = {
  width: '100%',
  height: '100%',
  position: 'fixed',
  backgroundImage: `url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/license/License.jpg')`,
  backgroundPosition: 'center',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
};

const LevelupCertificate = ({ data }) => {
  const ref = useRef(null);

  return (
    <>
      <ReactToPrint
        trigger={() => (
          <EditBtnSM className="absolute top-[20px] right-[20px] bg-[#eeeeee] z-50">
            <PrintIcon color="primary" />
          </EditBtnSM>
        )}
        content={() => ref.current}
      />
      <div style={LevelUpImg} className="levelup_ref" ref={ref}>
        <LevelInfo data={data} />
      </div>
    </>
  );
};

export default LevelupCertificate;
