import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';


const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  width: theme.button.common.width.sm,
  height: theme.button.common.height.sm,
  padding: theme.button.common.paddingSize.sm,
  fontSize: theme.font.size.sm,
  fontWeight: theme.font.weight.bold,
  color: theme.button.black.color,
  background: theme.button.black.background,
  border: `1px solid ${theme.button.black.border}`,
  borderRadius: theme.button.common.radius,
  transition: 'all 0.3s ease',
  whiteSpace: 'nowrap',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    background: theme.button.black.hover.background,
    // color: theme.button.black.hover.color,
    border: `1px solid ${theme.button.black.hover.border}`,
  },
  '&:disabled': {
    boxShadow: 'none',
    opacity: 0.5,
  },

}));

const BlackBtnSM = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default BlackBtnSM;