import React, { useState } from 'react';
import styled from '@mui/material/styles/styled';
import { Box } from '@mui/material';

const ImageBox = styled(Box)(({ src, width, height, shape }) => ({
  width: width,
  height: height,
  backgroundImage: `url(${src})`,
  backgroundSize: 'cover',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'center 20%',
  backgroundColor: '#ffffff',
  borderRadius: shape,
  transition: 'all 0.3s ease',
  border: '1px solid #e7e7e7',
}));

const CertificateProfile = React.forwardRef((props, ref) => {
  const defaultProps = {
    // src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/default_image.png',
    src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/profile/defalut_image.jpg', // 아이콘 수정 경로
    size: '40px',
    shape: '50%',
    disabled: true,
  };
  
  const newProps = { ...props };

  const { width, height, shape, disabled } = newProps;

  const [src, setSrc] = useState('');
  const urlKey = props?.urlKey;
  const previewUrl = props?.previewUrl;
  // const { isLoading } = useQuery(['getProfileImg', urlKey], () => getProfileImg(urlKey), {
  //   enabled: !!urlKey && !previewUrl,
  //   onSuccess: (res) => {
  //     setSrc(res?.photo_url)
  //   }
  // })
  const getSrc = () => {
    if (previewUrl) {
      return previewUrl;
    }
    if (urlKey) {
      return `https://tkdw-img.s3.ap-northeast-2.amazonaws.com/${urlKey}`;
    }
    return defaultProps.src;
  };
  const styles = {
    src: getSrc(),
    width: width,
    height: height,
    shape: props.full ? '' : shape,
  };

  return (
    <div>
      <ImageBox {...styles} />
      <input ref={ref} type="file" accept="image/*" onChange={props?.onChange} className="hidden" />
    </div>
  );
});

export default CertificateProfile;
